import React, { useEffect } from "react";
import history from "../../history";

export default function HomePage() {
  return (
    <div className="container-fluid">
      <div
        className="alert-css alert alert-undefined alert-dismissible fade hide"
        role="alert"
        style={{ zIndex: 9999 }}
      >
        <button type="button" className="close">
          <span aria-hidden="true">×</span>
        </button>
      </div>
      <div style={{ display: "flex", justifyContent: "space-between" }}>
        <h4 className="h4 title_content mb-0 text-gray-800">Chỉnh sửa học viên</h4>
      </div>
      <div className="card shadow mb-4">
        <div className="card-body">
          <section className="content">
            <div className="row">
              <div className="col-md-12 col-xs-12">
                <div id="messages" />
                <div className="box">
                  <form role="form" method="post">
                    <div className="box-body">
                      <div className="form-group">
                        <label htmlFor="product_name">Tên học viên</label>
                        <input
                          type="text"
                          className="form-control"
                          name="name"
                          placeholder="Nhập tên nhân viên"
                          autoComplete="off"
                          
                        />
                      </div>
               
                      <div className="form-group">
                        <label htmlFor="product_name">Tên đăng nhập</label>
                        <div className="input-group mb-3">
                          <div className="input-group-prepend">
                            <span
                              className="input-group-text"
                              id="basic-addon1"
                            >
                              thuytrang_
                            </span>
                          </div>
                          <input
                            type="text"
                            className="form-control"
                            name="username"
                            placeholder="Nhâp tên tài khoản"
                            aria-label="Username"
                            aria-describedby="basic-addon1"
                            
                          />
                        </div>
                      </div>
                      <div className="form-group">
                        <label htmlFor="product_name">Mật khẩu</label>
                        <input
                          type="text"
                          className="form-control"
                          id="txtTitle"
                          name="password"
                          placeholder="Nhập mật khẩu"
                          autoComplete="off"
                          
                        />
                      </div>
                      <div className="form-group">
                        <label htmlFor="product_name">Điện thoại</label>
                        <input
                          type="text"
                          className="form-control"
                          id="txtTitle"
                          name="phone_number"
                          placeholder="Nhập SDT"
                          autoComplete="off"
                          
                        />
                      </div>
                     
                      <div className="form-group">
                        <label htmlFor="product_name">Giới tính</label>
                        <select
                          name="sex"
                          id="input"
                          className="form-control"
                          required
                        >
                          <option value={1}>Nam</option>
                          <option value={2}>Nữ</option>
                          <option value={0}>Không xác định</option>
                        </select>
                      </div>
                      <div className="form-group">
                        <label htmlFor="product_name">Địa chỉ</label>
                        <input
                          type="text"
                          className="form-control"
                          id="txtTitle"
                          name="address"
                          placeholder="Nhập địa chỉ"
                          autoComplete="off"
                          
                        />
                      </div>
                      <div className="form-group">
                        <label htmlFor="product_name">Email</label>
                        <input
                          type="text"
                          className="form-control"
                          id="txtTitle"
                          name="email"
                          placeholder="Nhập Email"
                          autoComplete="off"
                          
                        />
                      </div>
             
                    </div>
                    <div className="box-footer">
                      <button type="submit" className="btn btn-info   btn-sm">
                        <i className="fas fa-save" /> Lưu
                      </button>
                      <button
                      type="button"
                                                                    onClick={()=>{history.goBack()}}

                        className="btn btn-warning   btn-sm"
                        style={{ marginLeft: "10px" }}
                      >
                        <i className="fas fa-arrow-left" /> Trở về
                      </button>
                    </div>
                  </form>
              
                </div>
              </div>
            </div>
          </section>
        </div>
      </div>
    </div>
  );
}
